import React from "react"
import * as Styled from "./styled"
import { Fade } from "../Animations"
import Container from "../Container"
import Linebreaker from "../Linebreaker"

export default (props) => {
    return (
        <Styled.Wrapper>
            <Container hasVerticalLine={true} animateLine={true} isFirst={true}>
                <Linebreaker>
                    <Fade>
                        <Styled.Meta>
                            {props.date}
                            {props.category &&
                                <>
                                    <span> in</span> {props.category}
                                </>
                            }
                        </Styled.Meta>
                        <Styled.Title>
                            {props.title}
                        </Styled.Title>
                        <Styled.Dek>
                            {props.dek}
                        </Styled.Dek>
                    </Fade>
                </Linebreaker>
            </Container>
        </Styled.Wrapper>
    )
}